import React from 'react';
import { HashLink } from 'react-router-hash-link';

const NavLinks = () => {
    return (
        <>
            <HashLink className="px-4 font-extrabold text-gray-500 hover:text-purple-900" smooth to="/#about">
                Conocenos
            </HashLink>
            <HashLink className="px-4 font-extrabold text-gray-500 hover:text-purple-900" smooth to="/#services">
                Servicios
            </HashLink>
            {/* <HashLink className="px-4 font-extrabold text-gray-500 hover:text-blue-900" to="/">
                Portfolio
            </HashLink> */}
            <HashLink className="px-4 font-extrabold text-gray-500 hover:text-purple-900" to="/contact#contact">
                Contáctanos
            </HashLink>
            <HashLink className="text-white bg-purple-900 hover:bg-purple-800 inline-flex items-center justify-center w-auto px-6 py-3 shadow-xl rounded-xl" smooth to="/get-demo#demo">
                Solicita una Cotización
            </HashLink>
        </>
    )
}

export default NavLinks;
