import React from 'react';
import eaglesfinancialplanning from '../images/clients/eaglesfinancialplanning.jpeg';
import empireroofing from '../images/clients/empireroofing.png';
import daymondprotectiongroup from '../images/clients/daymondprotectiongroup.png';

const clientImage = {
    height: '10rem',
    width: 'auto',
    mixBlendMode: 'colorBurn'
}

const Clients = () => {
    return (
        <div className="mt-8 bg-gray-100">
            <section data-aos="fade-up">
                <div className="my-4 py-4">
                        <h2 className="my-2 text-center text-3xl text-purple-900 uppercase font-bold">Clientes</h2>
                        <div className='flex justify-center'>
                            <div className='w-24 border-b-4 border-purple-900'></div>
                        </div>
                        <h2 className="mt-4 mx-12 text-center text-xl lg:text-2xl font-semibold text-purple-900">Algunos de nuestros clientes.</h2>
                    </div>

                <div className="p-16" data-aos="fade-in" data-aos-delay="600">
                    <div className="grid sm:grid-cols-3 lg:grid-cols-3">
                        <div style={clientImage} className= "overflow-hidden flex justify-center transition-all ease-in-out opacity-80 hover:opacity-100 w-1/6">
                            <img src={eaglesfinancialplanning} alt="client" />                           
                        </div>

                        <div style={clientImage} className="overflow-hidden p-3 flex justify-center transition-all ease-in-out opacity-80 hover:opacity-100">
                            <img src={daymondprotectiongroup} alt="client" />                            
                        </div> 

                        <div style={clientImage} className="overflow-hidden p-3 flex justify-center transition-all ease-in-out opacity-80 hover:opacity-100">
                            <img src={empireroofing} alt="client" />                            
                        </div>

                                           
                    </div>
                </div>
            </section>
        </div>
    )
}

export default Clients;